const FRAME_DOT_RADIUS = 5;

class Frame {
    constructor(x, y, width, height) {
        this.x = x;
        this.y = y;
        this.width = width;
        this.height = height;
    }

    get rect() {
        return {
            x: this.x,
            y: this.y,
            width: this.width,
            height: this.height
        };
    }

    draw(ctx, rect) {
        if (!ctx) {
            throw new Error('context is undefined');
        }

        rect && (function (o) {

            console.log('call');

            o.x = rect.x || o.x;
            o.y = rect.y || o.y;
            o.width = rect.width || o.width;
            o.height = rect.height || o.height;

        })(this);

        ctx.beginPath();
        ctx.rect(this.x, this.y, this.width, this.height);
        ctx.strokeStyle = 'gray';
        ctx.lineWidth = 1;
        ctx.stroke();

        let pos = [{
                x: this.x,
                y: this.y
            },
            {
                x: this.x,
                y: this.y + this.height
            },
            {
                x: this.x + this.width,
                y: this.y
            },
            {
                x: this.x + this.width,
                y: this.y + this.height
            }
        ];

        for (let i = 0, len = pos.length; i < len; ++i) {
            ctx.beginPath();
            ctx.arc(pos[i].x, pos[i].y, FRAME_DOT_RADIUS, 0, 2 * Math.PI, false);
            ctx.fillStyle = 'gray';
            ctx.fill();
        }
    }
}

class Text extends Frame {
    constructor(x, y, width, height, text, fontSize, color) {
        super(x, y, width, height);
        this.font = `${fontSize || '36px'} Arial`;
        this.color = color || '#000';
        this.text = text || '';
    }

    draw(ctx, rect, select) {
        select && super.draw(ctx, rect);

        ctx.beginPath();
        ctx.font = this.font;
        ctx.textBaseline = 'top';
        ctx.fillStyle = this.color;
        ctx.fillText(this.text, this.x, this.y, this.width);

    }
}

module.exports = Text;