       import './css/main.scss';
       
       var BackGround = require('./js/components/background');
        var Text = require('./js/components/frame');

        const WIDTH = 900,
            HEIGHT = 550,
            RADIUS = 5;

        var canvas = document.querySelector('#canvas');
        canvas.width = WIDTH;
        canvas.height = HEIGHT;
        var ctx = canvas.getContext('2d');

        var bg = new BackGround(WIDTH, HEIGHT, 'yellow');
        bg.draw(ctx);

        var f = new Text(200, 200, 100, 100, 'hello');
        f.draw(ctx);

        let componentStack = [],
            background = bg;

        componentStack.push({
            weight: 0,
            x: f.rect.x,
            y: f.rect.y,
            width: f.rect.width,
            height: f.rect.height,
            component: f,
            selected: false
        });

        let isResize = false,
            isMove = false,
            target = null;

        canvas.onmousedown = function (e) {
            let x = e.layerX,
                y = e.layerY;

            let cp = componentStack;

            ctx.clearRect(0, 0, 900, 550);
            background.draw(ctx); // 背景首先渲染

            cp.forEach(cur => {
                cur.component.draw(ctx, {
                    x: cur.x,
                    y: cur.y,
                    width: cur.width,
                    height: cur.height
                }, false);
                cur.selected = false;
            });

            for (let i = 0, len = cp.length; i < len; ++i) {
                let cur = cp[i];
                // check resize
                if (
                    (x > cur.x - RADIUS && x < cur.x + RADIUS && y > cur.y - RADIUS && y < cur.y + RADIUS) ||
                    (x > cur.x + cur.width - RADIUS && x < cur.x + cur.width + RADIUS && y > cur.y - RADIUS && y <
                        cur.y - RADIUS) ||
                    (x > cur.x - RADIUS && x < cur.x + RADIUS && y > cur.y + cur.height -
                        RADIUS && y < cur.y + cur.height - RADIUS) ||
                    (x > cur.x + cur.width - RADIUS && x < cur.x + cur.width + RADIUS && y > cur.y + cur.height -
                        RADIUS && y < cur.y + cur.height - RADIUS)
                ) {
                    console.log('resize');
                    isResize = true;
                    target = cur.component;
                    return;
                }

                // check move
                if (x > cur.x && x < cur.x + cur.width && y > cur.y && y < cur.y + cur.height) {
                    console.log('move');
                    isMove = true;
                    target = cur;
                    cur.selected = true;
                    cur.component.draw(ctx, null, true);
                    return;
                }
            }
        }

        canvas.onmousemove = function (e) {
            if (!isMove && !isResize) return;
            let x = e.layerX,
                y = e.layerY;
            if (isMove) {
                canvas.style.cursor = 'move';
                ctx.clearRect(0, 0, 900, 550);
                let cp = componentStack;
                background.draw(ctx); // 背景首先渲染
                for (let i = 0, len = cp.length; i < len; ++i) {
                    let cur = cp[i];
                    cur.component.draw(ctx, {
                        x: cur.selected ? (cur.x = x) : cur.x,
                        y: cur.selected ? (cur.y = y) : cur.y,
                        width: cur.width,
                        height: cur.height
                    }, cur.selected);
                }
            }
        }

        canvas.onmouseup = function (e) {
            console.log('up');
            isMove = false;
            isResize = false;
            let cps = componentStack;
            canvas.style.cursor = 'default';
            cps.forEach(cp => {
                cp.selected = false;
            });
        }